import Vue from 'vue';

import Component from 'vue-class-component';
import { State } from 'vuex-class';

import Cache, { CACHE_NAME } from '~/cache';
import { IRedPacketDetailParams, IRecommendParams } from 'src/entity/route_params';
import { ITask } from '~/entity/task/index';
import Utils from '~/utils';

@Component({})
export default class RedPacketDetailComp extends Vue {

    // 红包任务
    receivedTask: ITask = null;

    @State(state => state.user.user)
    myInfo;

    activated() {
        const params: IRedPacketDetailParams = this['$route'].params;
        this.receivedTask = Utils.getItem(CACHE_NAME.redpacketDetail + params.taskId);
        // this.receivedTask = Cache.get(CACHE_NAME.redpacketDetail + params.taskId);
        console.log(this.receivedTask);
    }
    
    toRecommendPage() {
        const params: IRecommendParams = {
            action: 'task',
            uid: this.receivedTask.fromUid,
            subId: this.receivedTask.subsetTasks[0].subId
        };
        this['$router'].replace({
            name: 'recommend',
            params
        });
    }
}



